import request from '@/apis/request.wms'

// 
export function getBoardListApi(data = {}) {
  return request({
    url: '/statistics/wmsBillboards/list',
    method: 'post',
    data
  })
}

// 
export function saveBoardApi(data = {}) {
  return request({
    url: '/statistics/wmsBillboards/save',
    method: 'post',
    data
  })
}

