<template>
  <div>
    <el-scrollbar class="scroll-container">
      <el-form ref="Form1" :model="form" :rules="rules" label-width="120px" label-position="right">
        <el-form-item label="" prop="region">
          <el-select v-model="form.region" placeholder="" filterable clearable style="width: 178px;">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" placeholder="" clearable style="width: 178px;" />
        </el-form-item>
      </el-form>
      <el-form ref="Form2" :model="form" :rules="rules" label-width="120px" label-position="top">
        <el-form-item label="" prop="chartIds" label-width="320px">
          <el-checkbox-group v-model="form.chartIds">
            <el-checkbox v-for="(chartTemItem, index) in chartTems" :key="index" :border="true" :label="chartTemItem.id">{{ chartTemItem.name }}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </el-scrollbar>
    <div style="text-align: right;">
      <el-button @click="handlePreview"> </el-button>
      <el-button type="primary" @click="handleSave"> </el-button>
      <el-button @click="handleCancel"> </el-button>
    </div>
  </div>
</template>

<script>
import html2canvas from 'html2canvas'
import { getBoardDetailApi, getchartListApi, saveBoardApi } from '@/apis/modules/analysis/board'

const INIT_FORM = () => ({
  region: '', // 
  name: '', // 
  chartIds: [] // 
})
const checkRegion = (rule, value, callback) => {
  if (!value) {
    callback(new Error(''))
  } else {
    callback()
  }
}
const checkName = (rule, value, callback) => {
  if (!value) {
    callback(new Error(''))
  } else {
    callback()
  }
}
const checkChartIds = (rule, value, callback) => {
  if (!value.length) {
    callback(new Error('1'))
  } else if (value.length > 9) {
    callback(new Error('9'))
  } else {
    callback()
  }
}
export default {
  props: {
    // ID
    id: {
      type: [String, Number],
      default: ''
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      options: [
        { label: '', value: 1 },
        { label: '', value: 2 }
      ],
      chartTems: [],
      rules: {
        region: [
          { validator: checkRegion, trigger: 'change' }
        ],
        name: [
          { validator: checkName, trigger: 'blur' }
        ],
        chartIds: [
          { validator: checkChartIds, trigger: 'change' }
        ]
      }
    }
  },
  mounted() {
    // 
    this.id && this.initInfo()
    // 
    this.initChartList()
  },
  methods: {
    handlePreview() {
      Promise.all([
        this.$refs['Form1'].validate(),
        this.$refs['Form2'].validate()
      ])
        .then(() => {
          // 
          this.$router.push({
            name: 'PreviewKanban',
            params: {
              ids: this.form.chartIds.join()
            },
            query: {
              //  
              ...this.id && { extraInfo: {
                ...this.$route.params
              }}
            }
          })
        })
        .catch(() => {
          // 
          console.log('error submit!!')
          return false
        })
    },
    initInfo() {
      const params = { id: this.id }
      return new Promise((resolve, reject) => {
        getBoardDetailApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.form = { ...data }
              this.$set(this.form, 'chartIds', data.chartTemplates.map(item => item.id))
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    initChartList() {
      const params = {}
      return new Promise((resolve, reject) => {
        getchartListApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.chartTems = [...data]
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    handleSave() {
      Promise.all([
        this.$refs['Form1'].validate(),
        this.$refs['Form2'].validate()
      ])
        .then(() => {
          // 
          const params = {
            entity: {
              ...this.id && { id: this.id },
              region: this.form.region, // 
              name: this.form.name, // 
              chartIds: this.form.chartIds.join() // 
            }
          }
          saveBoardApi(params).then(res => {
            const { code } = res
            if (!code) {
              this.id ? this.$notify.success({ title: '', message: '' }) : this.$notify.success({ title: '', message: '' })
              this.$store.dispatch('tagsView/delView', this.$route)
              this.$router.push({
                name: 'KanbanConfig'
              })
            }
          }).catch(err => {
            console.log('err', err)
          })
        })
        .catch(() => {
          // 
          console.log('error submit!!')
          return false
        })
    },
    handleCancel() {
      this.$store.dispatch('tagsView/delView', this.$route)
      this.$router.push({
        name: 'KanbanConfig'
      })
    },
    resetForm() {
      this.$refs['Form1'].resetFields()
      this.$refs['Form2'].resetFields()
    },
    //  base64
    getShareImgBase64() {
      return new Promise((resolve) => {
        setTimeout(() => {
          // #capture  DOM 
          html2canvas(document.querySelector('#capture'), {
            useCORS: true, // 
            scale: window.devicePixelRatio < 3 ? window.devicePixelRatio : 2,
            allowTaint: true // 
          }).then((canvas) => {
            const imgData = canvas.toDataURL('image/jpeg', 1.0)
            this.imgData = imgData
            resolve(imgData)
          })
        }, 300) //  300ms  DOM 
      })
    },
    // base64blob
    base64toBlob(dataurl) {
      const arr = dataurl.split(',')
      const mime = arr[0].match(/:(.*?);/)[1]
      const bstr = atob(arr[1])
      let n = bstr.length
      const u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], { type: mime })
    },
    // blobfile
    blobToFile(theBlob, fileName) {
      theBlob.lastModifiedDate = new Date()
      theBlob.name = fileName
      return theBlob
    },
    // blobfile
    changeBase2File(base64Data, fileName) {
      //  dataURLtoBlob  blobToFile file
      //! base64file
      const blob = this.base64toBlob(base64Data)
      const file = this.blobToFile(blob, fileName)
      return file
    }
    // const fileData = this.changeBase2File(base64Data, Date.now() + '.png')
  }
}
</script>

<style scoped lang="scss">
.scroll-container {
    height: calc(100vh - 150px);
}
::v-deep .el-scrollbar {
    .el-scrollbar__wrap {
        overflow: hidden;
    }
    .el-scrollbar__bar {
        // height: 0;
        &.is-horizontal {
            height: 0;
        }
        &.is-vertical {
            width: 0;
        }
    }
}
</style>

